<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base"></span>
    </el-checkbox>
    <form-profile ref="formProfileRef" :checked="checked"></form-profile>
    <list-profile class="px-4" title="" ref="normalProfileRef"></list-profile>
    <list-profile class="px-4 mt-4" title="" :showAddBtn="true" ref="medicalProfileRef" ></list-profile>
    <file-upload class="px-4 pt-5" title="" ref="profileUploadRef" desc="1-4"></file-upload>
  </div>
</template>

<script>
import FormProfile from "./FormProfile.vue"
import ListProfile from "./ListProfile.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FormProfile,
    ListProfile,
    FileUpload,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formProfileRef.resetRequiredForm(value)
      this.$refs.normalProfileRef.resetRequiredValue(value)
      this.$refs.medicalProfileRef.resetRequiredValue(value)
      this.$refs.profileUploadRef.resetRequiredFile(value)
      this.$bus.emit('fromProfileCheckbox', value)
    },
  },
  created() {
    this.$nextTick(() => {
      this.$refs.normalProfileRef.list = [
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "(SBP)(DBP)",
        },
        {
          id: "",
          content: "",
        },
      ]
      this.$refs.medicalProfileRef.list = [
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "24",
        },
        {
          id: "",
          content: "()",
        },
        {
          id: "",
          content: "()",
        },
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "3",
        },
      ]
    })
  },
}
</script>
