<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base"></span>
    </el-checkbox>
    <form-discussion ref="discussionFormRef" :checked="checked"></form-discussion>
    <file-upload class="px-4" title="" :showDownloadBtn="true" ref="discussMedicalUploadRef" templateUrl="/static/.xlsx"></file-upload>
    <file-upload class="px-4" title="" ref="discussFileRef" desc="" countContent=" 5-10 "></file-upload>
  </div>
</template>

<script>
import FormDiscussion from "./FormDiscussion.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FormDiscussion,
    FileUpload,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.discussionFormRef.resetRequiredForm(value)
      this.$refs.discussMedicalUploadRef.resetRequiredFile(value)
      this.$refs.discussFileRef.resetRequiredFile(value)
      this.$bus.emit('fromDiscussionCheckbox', value)
    },
  }
}
</script>
