      <div class="leading-none mr-2"><span class="text-[#F56C6C] mr-1" v-show="checked">*</span></div>
      <el-radio-group v-model="radio">
        <el-radio :label="item" v-for="(item, i) of radioList" :key="i">{{ item }}</el-radio>
      </el-radio-group>
    </div> -->
    <file-upload class="px-4" title="" :showDownloadBtn="true" ref="resultUploadRef"
